<template>
  <layout-default>
    <div class="kt-content  kt-grid__item kt-grid__item--fluid kt-grid kt-grid--hor" id="kt_content">
        <!-- begin:: Subheader -->
        <div class="kt-subheader   kt-grid__item" id="kt_subheader">
            <div class="kt-container  kt-container--fluid ">
                <div class="kt-subheader__main">
                    <h3 class="kt-subheader__title">
                        Services List
                    </h3>
                    <span class="kt-subheader__separator kt-hidden"></span>
                    <div class="kt-subheader__breadcrumbs">
                        <a href="javascript:;" class="kt-subheader__breadcrumbs-home"><i class="flaticon2-shelter"></i></a>
                        <span class="kt-subheader__breadcrumbs-separator"></span>
                        <a href="javascript:;" v-if="services" class="kt-subheader__breadcrumbs-link">
                            Show {{ services.data.length }} From {{ services.total }}
                        </a>
                        <a href="javascript:;" v-else class="kt-subheader__breadcrumbs-link">
                            <label class="mb-0">Not Found Any services Yet..</label>
                        </a> 
                    </div>
                </div>
                <div class="kt-subheader__toolbar">
                    <div class="kt-subheader__wrapper row">
                        <input type="text" id="search-input" placeholder="Search.." v-model="search" @change="fetchData" class="form-control col-12">
                    </div>
                </div>
            </div>
        </div>
        <!-- end:: Subheader -->
        <!-- begin:: Content -->
        <div class="kt-container  kt-container--fluid  kt-grid__item kt-grid__item--fluid">
            
            <div class="kt-portlet kt-portlet--mobile">
                <div class="kt-portlet__head kt-portlet__head--lg">
                    <div class="kt-portlet__head-label">
                        <span class="kt-portlet__head-icon">
                            <i class="kt-font-brand flaticon2-line-chart"></i>
                        </span>
                        <h3 class="kt-portlet__head-title">
                            View All Services
                        </h3>
                    </div>
                    <div class="kt-portlet__head-toolbar">
                        <div class="kt-portlet__head-wrapper">
                            <div class="kt-portlet__head-actions">
                                <a href="#" @click="$refs.ModalForm.ShowCreate()" @shortkey="$refs.ModalForm.ShowCreate()" v-shortkey="['+']" class="btn btn-brand btn-elevate btn-icon-sm">
                                    <i class="la la-plus"></i>
                                   <label class="mb-0" style="cursor: pointer">New Service</label>
                                </a>
                                <!--begin::Modal-->   
                                <!--end::Modal-->   
                            </div>
                        </div>
                    </div>
                </div>
                <div v-if="services" class="kt-portlet__body">
                    <table class="table table-hover">
                        <thead>
                            <tr>
                                <th scope="col">#</th>
                                <th scope="col">Name</th>
                                <th scope="col">Icon</th>
                                <th scope="col">Description</th>
                                <th scope="col">Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="service in services.data"  v-bind:key="service.id">
                                <td scope="row">{{ service.id }}</td>
                                <td>{{ service.name }}</td>
                                <td v-html="service.icon"></td>
                                <td>{{ service.description }}</td>
                               
                                <td>
                                    <span class="dropdown">
                                        <a href="#" class="btn btn-sm btn-clean btn-icon btn-icon-md" data-toggle="dropdown" aria-expanded="true">
                                            <i class="la la-ellipsis-h"></i>
                                        </a>
                                        <div class="dropdown-menu dropdown-menu-right">
                                            <router-link :to="{ name: 'service', params: { service_id: service.id } }" tag="a" class="dropdown-item">
                                                <i class="la la-eye"></i> Show
                                            </router-link>
                                            <!--
                                            <a class="dropdown-item" href="javascript:;" @click="$refs.ModalForm.ShowUpdate(service)"><i class="la la-edit"></i> Edit</a>
                                            <a class="dropdown-item" href="javascript:;" @click="$refs.ModalForm.deleteService(service.id)"><i class="la la-trash"></i> Delete</a>
                                            -->
                                        </div>
                                    </span>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <pagination-nav :data="services" :GoToPage="GoToPage" />
                </div> 
                
                <div v-else class="kt-portlet__body text-center">
                    <clip-loader v-if="loader" :loading="loader" color="#5578eb" size="50px"></clip-loader>
                    <label v-else >Not Found Any Services Yet..</label>
                </div>
                
            </div>
        </div>
    </div>
    <!-- end:: Content -->
  </layout-default>

</template>

<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/tables/pagination';
import axios from 'axios';

    export default {
        components: {
            LayoutDefault,
            PaginationNav,
        },
        watch: {
            $route: {
                immediate: true,
                handler(to, from) {
                    document.title = to.meta.title || 'VD';
                }
            },
        },
    data() {
        return {
            services: null,
            search: null,
            loader: true,
        };
    },
    created() {
        this.fetchData();
    },
    methods: {
        GoToPage(number) {
            this.services.current_page=number;
            this.fetchData();
        },
        fetchData() {
           let data = {params: {page:(this.services)?this.services.current_page:1,search: this.search}};
            axios.get('/api/services',data).then(response => {
                this.services=response.data.success.services;
                this.loader=false;
            });    
        },
    }
} 
 
</script>
