// pages
import Home from '../pages/Home'
import Login from "../pages/auth/Login";
import PageNotFound from '../pages/errors/404' 
//////////////////////////////////////////////////////////
import Users from "../pages/users/index";
import Services from "../pages/services/index"; 
import Service from "../pages/services/show"; 
// import SubCategories from "../pages/products/sub_categories"; 
//////////////////////////////////////////////////////////
// middlewares
import guest from "../middlewares/guest";
import auth from "../middlewares/auth";
import checkAuth from "../middlewares/auth-check";
import middlewarePipeline from "../routes/middlewarePipeline";

//////////////////////////////////////////////////////////
//
import VueRouter from "vue-router";
import Vue from "vue";
import store from "../store/index"; 
Vue.use(VueRouter);

const router = new VueRouter({
    mode:'history',
    routes:[
        //
        //clients
        {path:'/admin/services',name: 'services',component: Services,meta:{middleware: [auth, checkAuth],title: 'Services View'}},
        {path:'/admin/services/:service_id',name: 'service',component: Service,meta:{middleware: [auth, checkAuth],title: 'Service View'}},
        {path:'/admin/users',name: 'Users',component: Users,meta:{middleware: [auth, checkAuth],title: 'Users View'}},
        // {path:'/admin/clients/:client_id',name: 'ClientTransactions',component: ClientShow,meta:{middleware: [auth, checkAuth],title: 'Client Transactions View'}},
        //
        {path:'/admin/',name: 'home',component: Home,meta:{title: 'Home Page',middleware: [auth, checkAuth]}},
        {path:'/admin/login',name: 'login',component: Login,meta:{title: 'Login Page',middleware: [guest]}},
        {path:'/admin/*',name: '404',component: PageNotFound,meta:{title: '404'}},


    ],
    linkActiveClass: "", // active class for non-exact links.
    linkExactActiveClass: "kt-menu__item--active" // active class for *exact* links.
});  

router.beforeEach((to, from, next) => {
    
    if (!to.meta.middleware) return next();
    const middleware = to.meta.middleware;
    const context = {
        to,
        from,
        next,
        store
    };
    return middleware[0]({
        ...context,
        next: middlewarePipeline(context, middleware, 1)
    });
});

export default router;