<template>
    <div id="kt_header" class="kt-header kt-grid__item  kt-header--fixed ">
        <div class="kt-header-menu-wrapper" id="kt_header_menu_wrapper"></div>

        <!-- begin:: Header Topbar -->
        <div class="kt-header__topbar">
            
            <!--begin: User Bar -->
            <div class="kt-header__topbar-item kt-header__topbar-item--user">
                <div class="kt-header__topbar-wrapper" data-toggle="dropdown" data-offset="0px,0px">
                    <div class="kt-header__topbar-user">
                        
                        <span class="kt-header__topbar-welcome kt-hidden-mobile">Hi,</span>
                        <!--User Name -->
                        <span class="kt-header__topbar-username kt-hidden-mobile" v-if="user"><b class="size-15">{{user.name}}</b></span>
                        <!--User Image -->
                        <img class="" alt="Pic" src="/assets/media/users/default.jpg" />
                        <!--use below badge element instead the user avatar to display username's first letter(remove kt-hidden class to display it) -->
                        <span class="kt-hidden kt-badge kt-badge--username kt-badge--unified-success kt-badge--lg kt-badge--rounded kt-badge--bold">S</span>
                    </div>
                </div>
                <div class="dropdown-menu dropdown-menu-fit dropdown-menu-right dropdown-menu-anim dropdown-menu-top-unround dropdown-menu-xl">
                    <!--begin: Head -->
                    <div class="kt-user-card kt-user-card--skin-dark kt-notification-item-padding-x" style="background-image: url(/assets/media/misc/bg-1.jpg)">
                        <div class="kt-user-card__avatar">
                            <!--<img class="kt-hidden" alt="Pic" src="assets/media/users/default.jpg" />-->
                            
                            <!--use below badge element instead the user avatar to display username's first letter(remove kt-hidden class to display it) -->
                            <span class="kt-badge kt-badge--lg kt-badge--rounded kt-badge--bold kt-font-success" v-if="user"><b>{{user.name.charAt(0)}}</b></span>
                        </div>
                        <div class="kt-user-card__name size-15" v-if="user">
                            {{user.name}}                            
                        </div>
                        
                        <div class="kt-user-card__badge" style="cursor: pointer;" @click="logout">
                            <span class="btn btn-danger btn-sm btn-bold btn-font-lg size-15">LogOut</span>
                        </div>
                    </div>
                    <!--end: Head -->
                </div>
            </div>
            <!--end: User Bar -->
        </div>
        <!-- end:: Header Topbar -->
    </div>
</template>
<script>
import { mapGetters } from "vuex";
import LayoutDefault from '../layouts/App';
import axios from 'axios';

export default {
  components: {
    LayoutDefault,
  },
  methods: {
        logout() {
            axios.post('/api/logout').then(response => {
                this.$store.dispatch("auth/logout")
                this.$router.push({name: 'login'})
            })
            .catch((error) => {
                // this.$store.commit("auth/LOGIN_FAILED", error.response.data)
                console.log('error..');
            })
        }
  },
  computed: {
      ...mapGetters({
        user: "auth/user",
        // messages: "socket/messages",
      })
  },
}
</script>
