<template>
  <layout-default>
    <div class="kt-content  kt-grid__item kt-grid__item--fluid kt-grid kt-grid--hor" id="kt_content">
        <!-- begin:: Subheader -->
        <div class="kt-subheader   kt-grid__item" id="kt_subheader">
            <div class="kt-container  kt-container--fluid ">
                <div class="kt-subheader__main">
                    <h3 class="kt-subheader__title">
                        Service Page
                    </h3>
                    <span class="kt-subheader__separator kt-hidden"></span>
                    <div class="kt-subheader__breadcrumbs">
                        <a href="javascript:;" class="kt-subheader__breadcrumbs-home"><i class="flaticon2-shelter"></i></a>
                        <span class="kt-subheader__breadcrumbs-separator"></span>
                        <a href="javascript:;" v-if="service" class="kt-subheader__breadcrumbs-link">
                             {{ service.name }}
                        </a>
                        <a href="javascript:;" v-else class="kt-subheader__breadcrumbs-link">
                            <label class="mb-0">Not Found Any Data Yet..</label>
                        </a> 
                    </div>
                </div>
                <div class="kt-subheader__toolbar">
                    <div class="kt-subheader__wrapper row">
                    </div>
                </div>
            </div>
        </div>
        <!-- end:: Subheader -->
        <!-- begin:: Content -->
        <div class="kt-container  kt-container--fluid  kt-grid__item kt-grid__item--fluid">
            
            <div class="kt-portlet kt-portlet--mobile">
                <div class="kt-portlet__head kt-portlet__head--lg">
                    <div class="kt-portlet__head-label">
                        <span class="kt-portlet__head-icon">
                            <i class="kt-font-brand flaticon2-line-chart"></i>
                        </span>
                        <h3 class="kt-portlet__head-title">
                            View Service Details
                        </h3>
                    </div>
                    <div class="kt-portlet__head-toolbar">
                        <div class="kt-portlet__head-wrapper">
                            <div class="kt-portlet__head-actions">
                                
                                <!--begin::Modal-->   
                                <!--end::Modal-->   
                            </div>
                        </div>
                    </div>
                </div>
                <div v-if="service" class="kt-portlet__body">
                    <form @submit.prevent="save()">
                        <div class="modal-body">
                            <div class="form-group">
                                <label for="">Name</label>
                                <input v-model="form.name" type="text" id="name-inb" class="form-control" >
                            </div>
                            
                            <div class="form-group">
                                <label for="">Icone (HTML)</label>
                                <input v-model="form.icon" type="text" id="name-inb" class="form-control" >
                            </div>

                            <div class="form-group">
                                <label for="">Description</label>
                                <textarea cols="30" v-model="form.description" rows="10" class="form-control"></textarea>
                            </div>

                            <div class="form-group">
                                <label for="">Content</label>
                                  <vue-editor v-model="form.content" />
                            </div>
                            
                            

                        </div>
                        <div class="modal-footer">
                            <button v-if="this.form.id" type="submit" class="btn btn-primary"><i class="fa fa-save"></i>Update</button>
                            <button v-else type="submit" class="btn btn-primary"><i class="fa fa-save"></i>Save</button>
                        </div>
                        
                    </form>
                </div> 
                
                <div v-else class="kt-portlet__body text-center">
                    <clip-loader v-if="loader" :loading="loader" color="#5578eb" size="50px"></clip-loader>
                    <label v-else >Not Found Any Services Yet..</label>
                </div>
                
            </div>
        </div>
    </div>
    <!-- end:: Content -->
  </layout-default>

</template>

<script>
import LayoutDefault from '../../layouts/App';
import { VueEditor } from "vue2-editor";
import Swal from 'sweetalert2/dist/sweetalert2.js';
import 'sweetalert2/src/sweetalert2.scss';
import {Form} from 'vform';
import axios from 'axios';
export default {
    components: {
        LayoutDefault,
        VueEditor
    },
    watch: {
        $route: {
            immediate: true,
            handler(to, from) {
                document.title = to.meta.title || 'VD';
            }
        },
    },
    data() {
        return {
            form: new Form({id:null,name:'',icon:'',description:'',content:''}),
            service: null,
            loader:true
        };
    },
    created() {
        this.fetchData();
    },
    methods: {
        fetchData() {
            axios.get('/api/services/'+this.$route.params.service_id).then(response => {
                this.service = response.data.service;
                this.form.fill(this.service)
                this.loader=false
            });      
        },
        save() {
            this.form.put('/api/services/'+this.$route.params.service_id).then(response => {
                Swal.fire({icon: 'success',title: 'Done',text: 'service Updated Successfully'});
            });      
        },
    }
} 
 
</script>
