<template>
    <div class="kt-grid kt-grid--ver kt-grid--root">
        <div class="kt-grid kt-grid--hor kt-grid--root kt-login kt-login--v2 kt-login--signin" id="kt_login">
            <div class="kt-grid__item kt-grid__item--fluid kt-grid kt-grid--hor" style="background-image: url(/assets/media/bg/bg-1.jpg);">
                <div class="kt-grid__item kt-grid__item--fluid kt-login__wrapper" style="padding: 3% 2rem 1rem 2rem;">
                    <div class="kt-login__container">
                        <div class="kt-login__logo mb-0">
                            <a href="#">
                                <img src="/back/logo.png" class="mb-5 mt-5" style="width: 100%;">
                            </a>
                        </div>      
                        <div class="kt-login__signin">
                            <div class="kt-login__head">
                                <h3 class="kt-login__title">Login To Admin Panel</h3>
                            </div>
                            <form class="kt-form" form @submit.prevent="login" @keydown="form.onKeydown($event)">
                                
                                <alert-error :form="form" class="mt-3" message="There were some problems with your input."></alert-error>
                                
                                <div class="form-group mt-1">
                                    <input v-model="form.phone" type="text" name="phone" placeholder="Phone" 
                                        class="form-control" :class="{ 'is-invalid': form.errors.has('phone') }">
                                    <has-error :form="form" field="phone"></has-error>
                                </div>
                                <div class="form-group">
                                    <input v-model="form.password" type="password" name="password" placeholder="Password" 
                                        class="form-control" :class="{ 'is-invalid': form.errors.has('password') }">
                                    <has-error :form="form" field="password"></has-error>
                                </div>

                                <div class="row kt-login__extra">
                                    <div class="col">
                                        <label class="kt-checkbox">
                                            <input type="checkbox" v-model="form.remember" name="remember"> Remember me
                                            <span></span>
                                        </label>
                                    </div>
                                    <div class="col kt-align-right">
                                        <a href="javascript:;" id="kt_login_forgot" class="kt-link kt-login__link">Forget Password ?</a>
                                    </div>
                                </div>
                                <div class="kt-login__actions">
                                <button id="kt_login_signin_submit" type="submit" class="btn btn-pill kt-login__btn-primary">
                                    <clip-loader :loading="isLoading" color="#fff" size="20px"></clip-loader>
                                    <span v-if="!isLoading">sign in</span>
                                    <i class="fa fa-sign-in" aria-hidden="true"></i>
                                </button>
                                </div>
                            </form>
                        </div>
                        
                        <div class="kt-login__forgot">
                            <div class="kt-login__head">
                                <h3 class="kt-login__title">Forgotten Password ?</h3>
                                <div class="kt-login__desc">Enter your email to reset your password:</div>
                            </div>
                            <form class="kt-form" action="">
                                <div class="input-group">
                                    <input class="form-control" type="text" placeholder="Email" name="email" id="kt_email" autocomplete="off">
                                </div>
                                <div class="kt-login__actions">
                                    <button id="kt_login_forgot_submit" class="btn btn-pill kt-login__btn-primary">Request</button>&nbsp;&nbsp;
                                    <button id="kt_login_forgot_cancel" class="btn btn-pill kt-login__btn-secondary">Cancel</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Vue from 'vue'
import {Form,HasError,AlertError} from 'vform'
import {mapGetters} from 'vuex'
Vue.component(HasError.name, HasError)
Vue.component(AlertError.name, AlertError)

export default {
    data() {
        return {
            form: new Form({
                phone: '',
                password: '',
                remember: false
            }),
        }
    },
    methods: {
        login() {
            this.$store.dispatch("auth/login")
            this.form.post("/api/login").then(({data}) => {
                this.$store.commit("auth/LOGIN_SUCCESS", {
                    token: data.success.token,
                    user: data.success.user,
                    pharmacy: data.success.pharmacy,
                    remember: this.form.remember 
                })
                this.$router.push({name: 'home'})
            })
            .catch((error) => {
                this.$store.commit("auth/LOGIN_FAILED", error.response.data)
            })
        }
    },
    computed: {
        ...mapGetters({
            authError: 'auth/authError',
            isLoading: 'auth/isLoading'
        })
    }
}
</script>

<style scoped>
    .is-invalid {
        border-bottom: 1px solid #e64f5c
    }
</style>