<template>
    <nav aria-label="Page navigation example">
          <ul class="pagination">
              <li class="page-item" v-bind:class="{ 'disabled': data.current_page==1 }"><a class="page-link" @click="data.current_page--;GoToPage(data.current_page)" href="javascript:;">Previous</a></li>
              
              <li v-for="index in data.last_page" :key="index" class="page-item" v-bind:class="{ 'active': index==data.current_page }">
                  <a class="page-link" @click="data.current_page=index;GoToPage(data.current_page)" href="javascript:;">{{ index  }}</a>
              </li>
              <li class="page-item" v-bind:class="{ 'disabled': data.current_page==data.last_page }"><a class="page-link"  @click="data.current_page++;GoToPage(data.current_page)" href="javascript:;">Next</a></li>
          </ul>
      </nav>
</template>

<script>
export default {
  props: ['data','GoToPage'],
}
</script>