<template>
  <layout-default>
    <div class="kt-content  kt-grid__item kt-grid__item--fluid kt-grid kt-grid--hor" id="kt_content">
        <!-- begin:: Subheader -->
        <div class="kt-subheader   kt-grid__item" id="kt_subheader">
            <div class="kt-container  kt-container--fluid ">
                <div class="kt-subheader__main">
                    <h3 class="kt-subheader__title">
                         
                    </h3>
                    <span class="kt-subheader__separator kt-hidden"></span>
                    <div class="kt-subheader__breadcrumbs">
                        <a href="javascript:;" class="kt-subheader__breadcrumbs-home"><i class="flaticon2-shelter"></i></a>
                        <span class="kt-subheader__breadcrumbs-separator"></span>
                        <a href="javascript:;" v-if="users" class="kt-subheader__breadcrumbs-link">
                             {{ users.data.length }}  {{ users.total }}
                        </a>
                        <a href="javascript:;" v-else class="kt-subheader__breadcrumbs-link">
                            <label class="mb-0">  </label>
                        </a> 
                    </div>
                </div>
                <div class="kt-subheader__toolbar">
                    <div class="kt-subheader__wrapper row">
                        <input type="text" id="search-input" placeholder="Search.." v-model="search" @change="Filter" class="form-control col-12">
                    </div>
                </div>
            </div>
        </div>
        <!-- end:: Subheader -->
        <!-- begin:: Content -->
        <div class="kt-container  kt-container--fluid  kt-grid__item kt-grid__item--fluid">
            
            <div class="kt-portlet kt-portlet--mobile">
                <div class="kt-portlet__head kt-portlet__head--lg">
                    <div class="kt-portlet__head-label">
                        <span class="kt-portlet__head-icon">
                            <i class="kt-font-brand flaticon2-line-chart"></i>
                        </span>
                        <h3 class="kt-portlet__head-title">
                              
                        </h3>
                    </div>
                    <div class="kt-portlet__head-toolbar">
                        <div class="kt-portlet__head-wrapper">
                            <div class="kt-portlet__head-actions">
                                <!-- <a href="#" @click="openModalWindow" @shortkey="openModalWindow" v-shortkey="['+']" class="btn btn-brand btn-elevate btn-icon-sm">
                                    <i class="la la-plus"></i>
                                    <label class="mb-0">  </label>
                                </a> -->
                                <!--begin::Modal-->
                                <div class="modal fade" id="addNew" tabindex="-1" role="dialog" aria-labelledby="addNewLabel" aria-hidden="true">
                                    <div class="modal-dialog modal-dialog-centered" role="document">
                                        <div class="modal-content">
                                            <div class="modal-header">
                                                
                                                <h5 v-show="!editMode" class="modal-title" id="addNewLabel">  </h5>
                                                <h5 v-show="editMode" class="modal-title" id="addNewLabel">  </h5>
                                                
                                                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                                    <span aria-hidden="true">&times;</span>
                                                </button>
                                            </div>
                                            
                                            <form @submit.prevent="editMode ? updateClient() : createClient()">
                                                <div class="modal-body">
                                                    <div class="form-group">
                                                        <label for=""> </label>
                                                        <input v-model="form.name" type="text" name="name"
                                                        placeholder="" id="user-name"
                                                        class="form-control" :class="{ 'is-invalid': form.errors.has('name') }">
                                                        <has-error :form="form" field="name"></has-error>
                                                    </div>
                                                    <div class="form-group">
                                                        <label for=""> </label>
                                                        <input v-model="form.phone" type="text" name="phone"
                                                        class="form-control" :class="{ 'is-invalid': form.errors.has('phone') }">
                                                        <has-error :form="form" field="phone"></has-error>
                                                    </div>
                                                    <div class="form-group">
                                                        <label for=""> </label>
                                                        <input v-model="form.password" type="password" name="password"
                                                        class="form-control" :class="{ 'is-invalid': form.errors.has('password') }">
                                                        <has-error :form="form" field="password"></has-error>
                                                    </div>
                                                    
                                                </div>
                                                <div class="modal-footer">
                                                    <button type="button" class="btn btn-danger" data-dismiss="modal"></button>
                                                    <button v-show="editMode" type="submit" class="btn btn-primary"></button>
                                                    <button v-show="!editMode" type="submit" class="btn btn-primary"></button>
                                                </div>
                                                
                                            </form>
                                            
                                        </div>
                                    </div>
                                </div>
                                
                                <!--end::Modal-->
                               
                            </div>
                        </div>
                    </div>
                </div>
                <div v-if="users&&users.data.length>0" class="kt-portlet__body">
                    <table class="table table-hover">
                        <thead>
                            <tr>
                                <th scope="col">#</th>
                                <th scope="col"> </th>
                                <th scope="col"></th>
                                <th scope="col"></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="user in users.data"  v-bind:key="user.id">
                                <td scope="row">{{ user.id }}</td>
                                <td>{{ user.name }}</td>
                                <td>{{ user.phone }}</td>
                                <td>
                                    <span class="dropdown" v-if="AuthUser&&(AuthUser.id==user.id||AuthUser.role=='admin')">
                                        <a href="#" class="btn btn-sm btn-clean btn-icon btn-icon-md" data-toggle="dropdown" aria-expanded="true">
                                            <i class="la la-ellipsis-h"></i>
                                        </a>
                                        <div class="dropdown-menu dropdown-menu-right">
                                            <a class="dropdown-item" href="#" @click="editModalWindow(user)"><i class="la la-edit"></i> </a>
                                            <a class="dropdown-item" v-if="AuthUser.role=='admin'" href="#" @click="deleteUser(user.id)"><i class="la la-trash"></i> </a>
                                        </div>
                                    </span>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <nav aria-label="Page navigation example">
                        <ul class="pagination">
                            <li class="page-item" v-bind:class="{ 'disabled': users.current_page==1 }"><a class="page-link" @click="users.current_page--;fetchData()" href="#">Previous</a></li>
                            
                            <li v-for="index in users.last_page" :key="index" class="page-item" v-bind:class="{ 'active': index==users.current_page }">
                                <a class="page-link" @click="users.current_page=index;fetchData()" href="javascript:;">{{ index  }}</a>
                            </li>
                            <li class="page-item" v-bind:class="{ 'disabled': users.current_page==users.last_page }"><a class="page-link"  @click="users.current_page++;fetchData()" href="#">Next</a></li>
                        </ul>
                    </nav>
                </div>
                <div v-else class="kt-portlet__body text-center">
                   <label for="">      </label>
                </div>
                
            </div>
        </div>
    </div>
    <!-- end:: Content -->
  </layout-default>

</template>

<script>
import LayoutDefault from '../../layouts/App';
import {Form} from 'vform'
import axios from 'axios';
import { mapGetters } from "vuex";
    export default {
          computed: {
            ...mapGetters({
                AuthUser: "auth/user",
            })
        },
        components: {
            LayoutDefault,
        },
        watch: {
            $route: {
                immediate: true,
                handler(to, from) {
                    document.title = to.meta.title || 'Zezo';
                }
            },
        },
    data() {
        return {
            users: null,
            editMode: false,
            // asset: route,
            search: null,
            form: new Form({
                id: null,
                name : null,
                phone : null,
                password: null,
            }),
        };
    },
    created() {
        this.fetchData();
    },
    mounted() {
        var self=this
        //search Open
        $( "#addNew" ).on('shown.bs.modal',function(e){$("#user-name").focus();});
        //search Close
        $( "#addNew" ).on('hidden.bs.modal',function(e){$("#search-input").focus();});
        ///////////////////////////
    },
    sockets: {
        users(data) {
            this.fetchData();
        }
    },
    methods: {
        openModalWindow(){
            this.editMode = false
            this.form.reset();
            if(this.search){
                this.form.name=this.search;
            }
            $('#addNew').modal('show');
        },
        createClient(){
            this.form.post("/api/users").then(({data}) => {
                this.users = data.success.users;
                $('#addNew').modal('hide');
                this.search=null;
            })
            .catch((error) => {
               console.log("Error......")
            })
        },
        updateClient(){
            this.form.put("/api/users/"+this.form.id).then(({data}) => {
                this.users = data.success.users;
                $('#addNew').modal('hide');
            })
            .catch((error) => {
               console.log("Error......")
            })
        },
        
        editModalWindow(user){
            this.form.clear();
            this.editMode = true
            this.form.reset();
            $('#addNew').modal('show');
            this.form.fill(user)
        },
        deleteUser(id) {
            this.$swal.fire({
                title: 'Are you sure?',
                text: "You won't be able to revert this!",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Yes, delete it!'
            }).then((result) => {
                if (result.value) {
                    axios.delete('/api/users/'+id)
                    .then(response => {
                        console.log(response.data)
                        if(response.data.message){
                            this.$swal.fire({
                                icon: 'error',
                                title: 'Oops...',
                                text: response.data.message+'!',
                                footer: '<a href>Why do I have this issue?</a>'
                            });
                        }else{
                            this.users = response.data.success.users;
                            this.$swal.fire('Deleted!','User deleted successfully','success');
                        }
                    })
                    .catch(() => {
                        this.$swal.fire({
                            icon: 'error',
                            title: 'Oops...',
                            text: 'Something went wrong!',
                            footer: '<a href>Why do I have this issue?</a>'
                        });
                    });
                }
            })
        },
        fetchData() {
           let data = {
                    params: {page: (this.users)?this.users.current_page:1 }
                };
            axios.get('/api/users',data).then(response => {
                this.users = response.data.success.users;
            });    
        },
        Filter() {
            let data = {params: {search: this.search }};
            axios.get('/api/users',data).then(response => {this.users = response.data.success.users;
            });    
        },
    }
}

</script>

<style scoped>
    th,.dropdown-item,label,.modal-footer > button{
        font-weight: bold!important;
        font-size: 15px!important;
    }
    h5,h3{
        font-weight: bold!important;
        font-size: 20px!important;
    }
    .dropdown-menu > .dropdown-item [class*=" la-"] {
        font-size: 1.5rem!important;
    }

</style>